import {Navigate, Route, Routes, useNavigate} from 'react-router-dom';
import './App.css';
import Login from './component/customer/Login';
import { useEffect, useState } from 'react';
import Main from './component/default/Main';
import Join from "./component/customer/Join";
import Mypage from "./component/Mypage/Mypage";
import InfoModify from "./component/Mypage/InfoModify";
import Cart from "./component/customer/Cart";
import Payment from "./component/customer/Payment";
import SubOption from "./component/customer/SubOption";

function App() {
    const [message, setMessage] = useState([]);
    useEffect(() => {
        fetch("/api/demo-web")
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                setMessage(data);
            });
    }, []);

    let navigate = useNavigate();
    let [no, setNo] = useState(0); //    props  
    const [isLogin, setIsLogin] = useState(false); //   

    useEffect(() => {
        // session   null 
        if (sessionStorage.getItem("user_no") !== null) {
            setNo(sessionStorage.getItem("user_no")); // user_no() 
            //   
        } else if (localStorage.getItem("user_no") !== null) {
            setNo(localStorage.getItem("user_no")); // user_no() 
            // } else if (sessionStorage.getItem("admin") !== null) {
            //   //  
            //   setAdminId(sessionStorage.getItem("admin")); //  id
        }
    }, [no]);

    useEffect(() => {
        if (sessionStorage.getItem("user_no") !== null) {
            console.log("isLogin ===> ", isLogin);
            setIsLogin(true);
        } else if (localStorage.getItem("user_no") !== null) {
            console.log("isLogin ===> ", isLogin);
            setIsLogin(true);
            // } else if (sessionStorage.getItem("admin") !== null) {
            //   setIsAdmin(true);
            //   setIsLogin(true);
            //   console.log("isLogin ===> ", isLogin);
            //   console.log("isAdmin ===> ", isAdmin);
        } else {
            console.log("isLogin ===> ", isLogin);
            // console.log("isAdmin ===> ", isAdmin);
        }
        console.log("useEffect ");
    }, [isLogin]);

    // 
    const logout = () => {
        sessionStorage.clear();
        window.localStorage.removeItem("user_no");
        window.localStorage.removeItem("com.naver.nid.access_token");
        alert(".");
        navigate("/");
        window.location.reload();
    };

    return (
        <>
            <Routes>
                <Route
                    path="/"
                    exact={true}
                    element={
                        <Main isLogin={isLogin} logout={logout} no={message}/>
                    }
                />
                <Route
                    path="/Login"
                    exact={true}
                    element={
                        <Login isLogin={isLogin} no={no}/>
                    }
                />
                <Route
                    path="/Join"
                    exact={true}
                    element={
                        !isLogin ? <Join isLogin={isLogin}/> : <Navigate to="/"/>
                    }
                />
                <Route
                    path="/Cart/:userNo"
                    exact={true}
                    element={
                        <Cart isLogin={isLogin} logout={logout} no={no}/>
                    }
                />
                <Route
                    path={"/Mypage"}
                    exact={true}
                    element={
                        <Mypage isLogin={isLogin} logout={logout} no={no}/>
                    }
                />
                <Route
                    path={"/InfoModify"}
                    exact={true}
                    element={
                        <InfoModify/>
                    }
                />
            </Routes>
        </>
    );
}

export default App;
