import { Button, Container, Form, Modal } from "react-bootstrap"
import Header from "../default/Header"
import { useState } from "react";
import axios from "axios";
import DaumPostcodeEmbed from "react-daum-postcode";
import Footer from "../default/Footer";


const Join = (props) => {

    const register = (e) => {
        e.preventDefault();
        document.querySelector("#user_register").action =
          process.env.REACT_APP_SPRING_IP + "register";
        document.querySelector("#user_register").submit();
    };

    const [userId, setId] = useState("");
    const [password, setPassword] = useState("");
    const [zipcode, setZipcode] = useState(""); // 
    const [address, setAddress] = useState(""); // 
    const [show, setShow] = useState(false);

    //   
    const [passwordMessage, setPasswordMessage] = useState("");
    
    //  
    const [isId, setIsId] = useState(false);
    const [isPassword, setIsPassword] = useState(false);
    const [isName, setIsName] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    /////     //////
    const handleComplete = (data) => {
        let fullAddress = data.address;
        let extraAddress = "";

        if (data.addressType === "R") {
        if (data.bname !== "") {
            extraAddress += data.bname;
        }
        if (data.buildingName !== "") {
            extraAddress +=
            extraAddress !== "" ? `, ${data.buildingName}` : data.buildingName;
        }
        fullAddress += extraAddress !== "" ? ` (${extraAddress})` : "";
        }
        console.log(data);
        console.log(data.zonecode);
        console.log(fullAddress); // e.g. '  2 20 (1)'
        setZipcode(data.zonecode);
        setAddress(fullAddress);
        setShow(false);
    };

    //////     //////
    const Inputzipcode = (e) => {
        console.log(e.target.value);
        setZipcode(e.target.value);
    };
    const Inputaddress = (e) => {
        console.log(e.target.value);
        setAddress(e.target.value);
    };

    const chkId = (e) => {
        console.log(" ");
        axios
          .post(process.env.REACT_APP_SPRING_IP + "emailcheck", null, {
            params: { setId : userId },
          })
          .then((result) => {
            console.log(result);
            if (result.data === 1) {
              alert("  .");
              return () => {};
            } else {
              alert("  .");
            }
          })
          .catch((err) => {});
    };

    //   
    const chkPassword = (e) => {
        const passwordRegex =
        /^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{6,25}$/;
        const passwordCurrent = e.target.value;
        setPassword(passwordCurrent);
        console.log(passwordCurrent);
        if (!passwordCurrent || !passwordRegex.test(passwordCurrent)) {
            setIsPassword(false);
            setPasswordMessage(
                "++  6  ."
            );
        } else {
            setIsPassword(true);
            setPasswordMessage("  .");
        }
    };

    const chkName = (e) => {
        const nameCurrent = e.target.value;
        console.log(nameCurrent);
        if (!nameCurrent) {
          setIsName(false);
        } else {
          setIsName(true);
        }
    };

    const chkUser = (e) => {
        const idCurrent = e.target.value;
        console.log(idCurrent);
        setId(idCurrent);
        if (!idCurrent) {
          setIsId(false);
        } else {
          setIsId(true);
        }
    };

    return (
        <>
            <Header/>

            <Container>
                <h1 className='mb-5 mt-5' style={{textAlign: "center"}}>
                    
                </h1>

                <Form
                    method="post"
                    id="user_register"
                    style={{margin: "auto", width: "25rem"}}>

                    <Form.Group className="mb-3" controlId="formBasicId">
                        <Form.Floating className="mb-3">
                            <Form.Control className="mt-2"
                                          type="text"
                                          name="user_id"
                                          value={userId}
                                          onChange={chkUser}
                                          placeholder="Id"
                            />
                            <label htmlFor="InputId">Input Id</label>
                        </Form.Floating>
                        <Button
                            variant="outline-secondary"
                            type="button"
                            onClick={chkId}
                        >
                             
                        </Button>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Floating className="mb-3">
                            <Form.Control className="mt-2"
                                          type="password"
                                          name="user_password"
                                          value={password}
                                          onChange={chkPassword}
                                          placeholder="Password"
                            />
                            <label htmlFor="InputPassword">Password</label>
                        </Form.Floating>
                        {password.length > 0 && (
                            <div
                                className={`message ${isPassword ? "success" : "error"}`}>
                                {passwordMessage}
                            </div>
                        )}
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicName">
                        <Form.Floating className="mb-3">
                            <Form.Control className="mt-2"
                                          type="userName"
                                          name="user_name"
                                          onChange={chkName}
                                          placeholder="Name"
                            />
                            <label htmlFor="InputName">Name</label>
                        </Form.Floating>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicPhone">
                        <Form.Floating className="mb-3">
                            <Form.Control className="mt-2"
                                          type="text"
                                          name="phone"
                                          placeholder=""
                            />
                            <label htmlFor="InputPhone">PhoneNumber</label>
                        </Form.Floating>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        <Form.Floating className="mb-3">
                            <Form.Control className="mt-2"
                                          type="email"
                                          name="email"
                                          placeholder=""
                            />
                            <label htmlFor="InputPhone">Email</label>
                        </Form.Floating>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="InputZipcode">
                        <Form.Floating className="mb-3">
                            <Form.Control className="mt-2"
                                          type="text"
                                          name="user_zipcode"
                                          value={zipcode}
                                          onChange={Inputzipcode}
                                          placeholder=""
                                          readOnly
                            />
                            <label htmlFor="InputZipcode">zipcode</label>
                        </Form.Floating>
                        <Button
                            variant="outline-secondary"
                            type="button"
                            onClick={handleShow}
                        >
                             
                        </Button>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicAddress1">
                        <Form.Floating className="mb-3">
                            <Form.Control className="mt-2"
                                          type="text"
                                          name="user_address"
                                          value={address}
                                          onChange={Inputaddress}
                                          placeholder=""
                            />
                            <label htmlFor="InputAddress">Address</label>
                        </Form.Floating>
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicAddress2">
                        <Form.Floating className="mb-3">
                            <Form.Control className="mt-2"
                                          type="text"
                                          name="user_address_detail"
                                          placeholder=""
                            />
                            <label htmlFor="InputAddress"></label>
                        </Form.Floating>
                    </Form.Group>

                    <Form.Group className="mt-3 mb-4" controlId="formBasicSubmit" style={{float: "right"}}>
                        <Button variant="outline-secondary"
                                style={{margin: "auto", width: "10rem"}}
                                type="submit"
                                onClick={register}
                                disabled={!(isId && isPassword && isName)}
                        >
                            
                        </Button>
                    </Form.Group>
                </Form>

            </Container>

            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <Footer/>

            {/*    */}
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title> </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <DaumPostcodeEmbed onComplete={handleComplete} {...props} />
                </Modal.Body>
            </Modal>
            {/*    */}
        </>
    )
}

export default Join;