import {useLayoutEffect, useState} from "react";
import Header from "../default/Header";
import {useParams} from "react-router-dom";
import axios from "axios";

const Cart = ({isLogin, logout, no}) => {

    const { userNo } = useParams();
    console.log(userNo);
    const [cartList, setList] = useState(null);

    useLayoutEffect(() => {
        fetchCart();
    },[]);

    const fetchCart = () => {
        axios
            .get(`/Cart/${userNo}`)
            .then((res) => {
                const cartData = res.data[0];
                const {
                    cart_no =0,
                    user_no=0,
                    book_no=0,
                    book_count=0,
                } = cartData;
                setList({
                    cart_no,
                    user_no,
                    book_no,
                    book_count,
                });
            })
            .catch((err) => console.log(err));
    };

    if(!cartList){
        return null;
    }

    return(
        <>
            <Header isLogin={isLogin} logout={logout} no={no}/>

            <h1>
                Cart
            </h1>


            <p>
                cart_no : {cartList.book_count}
            </p>


        </>
    );
};

export default Cart;